body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

main {
  min-height: 100vh;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

nav.navbar {
  background-color: #4285f4;
}

nav a.navbar-brand {
  color: white;
}

span.navbar-toggler-icon {
  color: white!important;
}

.nav-link {
  color: rgba(255, 255, 255, .5);
}

.nav-link:hover {
  color: #db4437;
}

footer.footer {
    background-color: #4285f4;
    padding: 1rem;
}

footer span {
    color: white;
}
.jumbotron {
    background-size: cover;
    color: white;
    text-shadow: 2px 2px 8px gray;
    box-shadow: 0 8px 10px -5px gray;
    border-radius: 0rem;
    padding-top: 6rem;
    padding-bottom: 6rem; 
}

.form-control:focus {
    box-shadow: none;
  }
  
  .form-control-underlined {
    border-width: 0;
    border-bottom-width: 1px;
    border-radius: 0;
    padding-left: 0;
    border-color: #DB4437;
  }
.card {
  box-shadow: 0 8px 10px 0px gray;
  border: 10px solid #4285F4;
  padding: 1rem;
}

.card-img {
  width: 75%;
}

.card-col {
  padding: 1rem;
}

.card-text {
    max-height: 200px;
    overflow: scroll;
}

/* MEDIA QUERIES */
@media only screen and (max-width: 576px) {
  .card-img  {
    width: 40%;
  }
}


